.message {
  max-width: 670px;
  width: 100%;
}

.message_type_user {
  align-self: flex-end;
}

.message__text {
  text-align: start;
  padding: 20px;
  margin: 0;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  /* display: -webkit-box;
  -webkit-line-clamp: 14;
  -webkit-box-orient: vertical; */
}

.message__text_type_user {
  background-color: #f6e8ff;
  border-radius: 16px 16px 0px 16px;
}

.message__text_type_system {
  white-space: pre-line;
  background-color: #fff;
  border-radius: 16px 16px 16px 0px;
}

.message__text-container {
  text-align: start;
  padding: 20px;
  margin: 0;
}

.message__container {
  display: flex;
  justify-content: flex-end;
}

.message__container_type_system {
  justify-content: flex-start;
}

.message__decoration {
  align-self: flex-end;
  width: 0;
  height: 0;
  border-style: solid;
}

.message__decoration_type_system {
  border-width: 0 0 16px 20px;
  border-color: transparent transparent #ffffff transparent;
}

.message__decoration_type_user {
  border-width: 0px 20px 16px 0px;
  border-color: transparent transparent #f6e8ff transparent;
}

.message__copy-button {
  min-width: 24px;
  height: 24px;
  margin: 0 0 0 12px;
  border: none;
  cursor: pointer;
  background-image: url(../../../../images/copy-button.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-color: inherit;
}

.message__time {
  padding: 8px 0 0;
  margin: 0;
  color: rgba(0, 0, 0, 0.6);
  font-family: "Open Sans";
  font-size: 14px;
  line-height: 19px;
}

.message__time_type_user {
  text-align: end;
}

.message__time_type_system {
  text-align: start;
}
